/* jshint ignore:start */

define('elosh-client/config/environment', ['ember'], function(Ember) {
  var prefix = 'elosh-client';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (runningTests) {
  require("elosh-client/tests/test-helper");
} else {
  require("elosh-client/app")["default"].create({"name":"elosh-client","version":"0.0.0+47ad42de"});
}

/* jshint ignore:end */
